import React, { Component } from 'react'
//
import ReactCanvasNest from 'react-canvas-nest';
import "./login.css"
import { Form, Input, Button, Checkbox, Row, Col, notification } from 'antd';
import {login} from "../../api/request"

//Form
const layout = {
    labelCol: { span: 8},
    wrapperCol: { span: 16 },
};
const tailLayout = {
    wrapperCol: { offset: 8, span: 16 },
};

export default class Login extends Component {
    onFinish = values => {
        login(values.name,values.pwd).then(res=>{
           if(res.data.status===1){
               //
               this.openNotificationWithIcon('success', "")
               sessionStorage.setItem("token", res.data.token)
               sessionStorage.setItem("_id", res.data._id)
               sessionStorage.setItem("name", values.name)
               window.location.href = "/home"
           }else{
               this.openNotificationWithIcon('error', "")
           }
        })
    };
    onFinishFailed = errorInfo => {
        console.log('Failed:', errorInfo);
    };
    //
    openNotificationWithIcon = (type, msg) => {
        notification[type]({
            message: msg
        });
    };
    render() {
        return (
            <div className="allScroll">
                <ReactCanvasNest className='canvasNest' config={{ pointColor: ' 255, 255, 255 ', lineColor: "255,255,255", count: 98 }} style={{ zIndex: 0 }} />
                <Row>
                    <Col span={12} offset={8}>
                        <Form
                            {...layout}
                            name="basic"
                            layout="horizontal"
                            initialValues={{ remember: true }}
                            onFinish={this.onFinish}
                            onFinishFailed={this.onFinishFailed}
                        >
                            <Form.Item
                                label=""
                                name="name"
                                rules={[{ required: true, message: '!' }]}
                            >
                                <Input />
                            </Form.Item>

                            <Form.Item
                                label=""
                                name="pwd"
                                rules={[{ required: true, message: '!' }]}
                            >
                                <Input.Password />
                            </Form.Item>

                            <Form.Item {...tailLayout} name="remember" valuePropName="checked" >
                                <Checkbox style={{"color":"white"}}></Checkbox>
                            </Form.Item>

                            <Form.Item {...tailLayout}>
                                <Button type="primary" htmlType="submit"></Button>
                            </Form.Item>
                        </Form>
                    </Col>
                </Row>
                
            </div>
        )
    }
}
